






























import { Component, Vue } from 'vue-property-decorator';
import Signin from '@/components/Signin.vue'
import Signup from '@/components/Signup.vue'
import { Roles } from '@/const/Roles.ts'
@Component({
  components: {
    Signin,
    Signup
  },
  computed: {
    
  }
})
export default class NavMenu extends Vue{

activeIndex = '1'

// 登录
showSignin(){
  this.$store.commit('showSignin', true)
}

// 注册
showSignup(){
  this.$store.commit('showSignup', true)
}

get signined (){
  return this.$store.state.accounts.phone
}
get userInfo() {
  return this.$store.state.accounts
}

Roles = Roles

get roles() {
  return this.$store.state.signinAccount.Roles || []
}



isManager(roles: any) {
  for(const role of roles){
    if(role.name === Roles.LabLeader.value){
      return true
    }
  }
  return false
}

logOut(){
  this.$store.commit('reviseAccount', {})
  this.$store.commit('reviseSignin', {})
  this.showSignin()
}

  handleSelect(key: string, keyPath: string) {
    console.log(key, keyPath);
    switch(key){
      case '1': {
        window.location.hash = '#/'
        break
      }
      case '2': {
        window.location.hash = '#/info'
        break
      }
      case '3': {
        window.location.hash = '#/regis'
        break
      }
      case '4': {
        window.location.hash = '#/management/forms'
        break
      }
      default: {
        window.location.hash = '#/'
        break
      }
    }
  }
  mounted(){
    switch(window.location.hash){
      case '#/':{
        this.activeIndex = '1'
        break
      }
      case '#/info':{
        this.activeIndex = '2'
        break
      }
      case '#/regis':{
        this.activeIndex = '3'
        break
      }
      case '#/management':
      case '#/management/upload':
      case '#/management/forms':
      case '#/management/statistics': {
        this.activeIndex = '4'
        break
      }
      default: {
        this.activeIndex = '1'
        break
      }
    }
  }

}
